.slider {
	overflow: hidden; }
.slider__slide {
	@include css-lock(403, 670, 991, 1599, "min-height");
	@include cover;
	position: relative; }
.slider__content {
	text-align: center;
	display: table;
	@include css-lock(403, 670, 991, 1599, "min-height");
	width: 100%; }

.slick-prev, .slick-next {
	top: auto;
	bottom: 30px;
	z-index: 1; }
.slick-prev {
	@include s(prev);
	left: 50%;
	margin-left: -40px; }
.slick-next {
	@include s(next);
	right: 50%;
	margin-right: -40px; }
